import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Actionsheet from './sheet';
import send from './send.png';

class App extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			show: false
		}
		this.appData = this.appData.bind(this);
	}

	appData(data){

		this.props.sendData(data+ " "+ this.props.tip+", "+"\n")
	}

	onClick = () => {
		const { show } = this.state
		this.setState({show: !show})
	}
   
	onRequestClose = () => {
	  this.onClick()
	}
	onCancel = () =>{

	}
	sheetClick = () => {
		
	}
	render() {
	  return (
		<div className = "items">
		  <div className = "item" onClick={this.onClick}>{this.props.tip}</div>
   		  <Actionsheet actionData = {this.appData} show= {this.state.show} menus={this.props.menus} onRequestClose={this.onRequestClose} onClick={this.sheetClick} onCancel={this.onCancel}/>
		</div>
	  )
	}
  }
class Disp extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			menus: [{content: 'some'}, {content: '1'}, {content: '2'}, {content: '3'}, {content: "a lot of"}],
			string: "sms:&body=Hey dan! \nI want \n",
			display: "Hey dan! \r\nI want\r\n"
		}
		this.getData = this.getData.bind(this);
	}
	getData(e){
		const display = this.state.display;
		this.setState({display: display+e});
		const string = this.state.string;
		this.setState({string: string+e});
	}
	
	render(){
		return(
			<div className = "Wrapper">

				<div className="menu">
					<App tip = "bacon" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "chicken" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "beef" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "parmesan" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "chorizo" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "mozarella" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "kale" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "potato" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "beans" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "sausage" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "lamb" menus = {this.state.menus} sendData = {this.getData}/>
					<App tip = "pumpkin" menus = {this.state.menus} sendData = {this.getData}/>
				</div>
				<div className = "submit">
					<a href={this.state.string}>
						<img className = "send" src = {send} />
					</a>
					<a className = "String"> {this.state.display} </a>
				</div>
			</div>
			)
	  }
  }

// ========================================

ReactDOM.render(
  <Disp />,
  document.getElementById('root')
);

